    escapeHtml(str) {
        if (str === null || str === undefined) {
            return "";
        }
        return str
            .replace(/&/g, "&amp;")
            .replace(/</g, "&lt;")
            .replace(/>/g, "&gt;")
            .replace(/\"/g, "&quot;")
            .replace(/\'/g, "&#39;")
            .replace(/\//g, "&#x2F;");
    }

    /**
     * æ¾ç¤ºä¿¡æ¯æç¤ºæ¡
     * @param msgtype æ¶æ¯ç±»å«
     * @param message æç¤ºæ¶æ¯ä¸
     * @param opts åæ° æä¾å±æ§ fitContent:trueæ¯æèªå¨éåºå®½åº¦
     * @param submsg æç¤ºæ¶æ¯äºçº§
     */
    show(msgtype: string, message: string, opts?: {}, submsg = "") {
        if (document.activeElement) {
            (document.activeElement as any).blur();
        }
        const modalHeight = 180;
        let defaultOpts = {
            safeHtml: true,
            title: this.titleText,
            width: 390,
            height: modalHeight,
            showButtons: false,
            showMaxButton: false,
            resizable: false,
            initialState: {
                okText: this.okText,
                okHandle: () => {
                    this.close();
                },
            },
            class:
                "modal-message " +
                (msgtype ? "modal-message-type-" + msgtype : ""),
            showHeader: false,
        };
        defaultOpts = Object.assign(
            {},
            this.defaultMessagerConfig,
            defaultOpts
        );
        let extendOpts = {};

        switch (msgtype) {
            case "prompt":
                // å¼¹åºå¯¹è¯ç±»å
                extendOpts = { showHeader: true, modalHeight: 260 };
                break;
            case "error":
                // éè¯¯
                extendOpts = {
                    title: this.errorTitle,
                    width: 446,
                    showHeader: true,
                    showMaxButton: true,
                    fitContent: true,
                };
                break;
            case "exception":
                extendOpts = {
                    safeHtml: false,
                    title: this.errorTitle,
                    width: 446,
                    showHeader: true,
                    fitContent: true,
                };
                break;
            default:
                // é»è®¤å¶ä»
                extendOpts = { width: submsg ? 446 : 390, fitContent: true };
        }
        Object.assign(defaultOpts, extendOpts);
        if (opts) {
            Object.assign(defaultOpts, opts);
        }

        if (message && defaultOpts.safeHtml) {
            message = this.escapeHtml(message);
        }

        Object.assign(defaultOpts.initialState, {
            type: msgtype,
            message,
            msg: submsg,
            exception: opts && opts["exception"] ? opts["exception"] : null,
            showLines: opts && opts["showLines"] ? opts["showLines"] : 3,
            readonly: opts && opts["readonly"] ? opts["readonly"] : false,
        });

        if (defaultOpts["buttons"] && defaultOpts["buttons"].length) {
            if (
                !defaultOpts.initialState["buttons"] ||
                !defaultOpts.initialState["buttons"].length
            ) {
                defaultOpts.initialState["buttons"] = defaultOpts["buttons"];
            }
        }
        const _modal = this.bsModalService.show(MessagerComponent, defaultOpts);
        // _modal.content.buttons = defaultOpts['buttons'] || [];
        this.confirmSubject = new Subject<boolean>();
        if (this.modals.length > 0) {
            const modalContainerComponent = _modal.dialog;
            modalContainerComponent.instance.isShown = true;
            modalContainerComponent.instance.moveTo(
                15 * this.modals.length,
                15 * this.modals.length
            );
        }

        this.ngZone.runOutsideAngular(() => {
            // ç¬¬1ä¸ªæé®è·åç¦ç¹
            setTimeout(() => {
                const btnOk = _modal.content.btnOkEl
                    ? _modal.content.btnOkEl.nativeElement
                    : null;
                if (btnOk) {
                    btnOk["focus"]();
                } else {
                    const btns =
                        _modal.dialog.location.nativeElement.querySelectorAll(
                            ".modal-footer button"
                        );
                    if (btns.length) {
                        btns.item(0)["focus"]();
                    }
                }

                // const overlays = Array.from(document.body.querySelectorAll('body>div, body>farris-dialog>.farris-modal,body>.farris-modal')).filter(n => n).map(n => {
                //     return window.getComputedStyle(n).zIndex;
                // }).filter(n => n && n!== 'auto').map(n => {
                //     return parseInt(n, 10);
                // });
                const maxZindex = this.commonUtils.getFloatingLayerIndex();
                _modal.dialog.location.nativeElement.style.zIndex = maxZindex;
            });
        });

        this.msgMgr(_modal);
        return _modal;
    }

    private msgMgr(ref: BsModalRef) {
        const id = this.bsModalService.getModalsCount() + 1000;
        if (this.modals.length === 0) {
            this.modals.push({ id, ref });
        } else {
            if (!this.getModalById(id)) {
                this.modals.push({ id, ref });
            } else {
                this.getModalById(id).ref = ref;
            }
        }

        const cmp = this.getModalCmp();

        cmp.closed.subscribe((level: number) => {
            this.removeMessager(id);
            this.confirmSubject.unsubscribe();
        });

        this.currentId = id;
    }

    question(
        message: string,
        okCallback: () => void,
        cancelCallback?: () => void,
        msg?: string,
        fitContent = true
    ) {
        const questionDlg = this.show(
            "question",
            message,
            {
                initialState: {
                    okText: this.yesText,
                    okHandle: () => {
                        questionDlg.close();
                        if (okCallback) {
                            okCallback();
                        }
                    },
                    cancelText: this.noText,
                    cancelHandle: () => {
                        questionDlg.close();
                        if (cancelCallback) {
                            cancelCallback();
                        }
                    },
                },
                fitContent,
            },
            msg
        );
        return questionDlg;
    }

