    confirm(message: string): Observable<any>;
    confirm(message: string, msg?: string | number, fitContent?: boolean): Observable<any>;
    confirm(message: string, msg?: string, fitContent = true): Observable<any> {
        const confirmSubject = new Subject();

        const _msg = typeof msg === "number" ? null : msg;
        const confirmDlg = this.show(
            "question",
            message,
            {
                initialState: {
                    okText: this.okText,
                    okHandle: () => {
                        confirmDlg.close();
                        confirmSubject.next(true);
                    },
                    cancelText: this.cancelText,
                    cancelHandle: () => {
                        confirmDlg.close();
                        confirmSubject.next(false);
                    },
                },
                fitContent,
                closed: () => {
                    confirmSubject.unsubscribe();
                },
            },
            _msg
        );

        if (msg && typeof msg === "number") {
            confirmDlg.dialog.location.nativeElement.style.zIndex = msg;
        }

        return confirmSubject.asObservable();
    }

